@import './assets/css/quill.snow.css';

.ql-container {
    height: 500px !important;
}

.ql-syntax {
    position: relative;
    width: 100%;
    background: #eff6ff;
    border-radius: 5px;
    margin-bottom: 30px;
    padding: 10px;
    padding-top: 15px;
    cursor: pointer;
    overflow: auto;
    color: #032e7a;
    font-size: 14px;
    font-weight: 500;
    font-family: Inconsolata, PT mono, Roboto mono, Space mono, monospace;
    line-height: 1.5;
    text-align: left;
    white-space: pre;
    word-spacing: normal;
    word-break: normal;
    word-wrap: normal;
    tab-size: 4;
    -webkit-hyphens: none;
    hyphens: none;
}

.dark .ql-syntax {
    position: relative;
    width: 100%;
    background: #181823;
    border-radius: 5px;
    margin-bottom: 30px;
    padding: 20px;
    overflow: auto;
    color: #032e7a;
    color: #FFF;
    font-size: 17px;
    font-weight: 500;
    font-family: Inconsolata, PT mono, Roboto mono, Space mono, monospace;
    line-height: 1.5;
    text-align: left;
    white-space: pre;
    word-spacing: normal;
    word-break: normal;
    word-wrap: normal;
    tab-size: 4;
    -webkit-hyphens: none;
    hyphens: none;
}
